@import url(https://fonts.googleapis.com/css2?family=Prompt:wght@400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow-x: hidden;
  background-color: #121212;
}

#about .container .title {
  z-index: 1;
  font-size: 5rem;
  font-weight: bold;
  line-height: 1.0062em;
  color: white;
  margin: 40px;
  display: flex;
}

#about .container .about-me {
  width: 75%;
  height: auto;
  font-size: 1.5em;
  text-align: center;
  color: whitesmoke;
  line-height: 1;
  overflow: hidden;
}

#about .container .mail {
  margin-top: 40px;
  text-decoration: none;
  list-style: none;
  padding: 10px;
  background-color: #444;
  border-radius: 9px;
}

#about .container .mail a {
  color: whitesmoke;
  font-size: 1em;
  text-decoration: none;
  transition: 0.3s;
}

#about .container .mail:hover {
  background-color: #f1f1f1;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

#about .container .links {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 40px;
}

#about .container .links a {
  padding-right: 20px;
  color: gray;
  transition: 100ms;
}

#about .container .links a:hover {
  -webkit-transform-origin: center;
          transform-origin: center;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  color: white;
}

.anim {
  -webkit-animation: flow 10s ease-in-out infinite;
          animation: flow 10s ease-in-out infinite;
}

@-webkit-keyframes flow {
  0% {
    background-position: 0 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0 50%;
  }
}

@keyframes flow {
  0% {
    background-position: 0 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0 50%;
  }
}

.text-transform {
  -webkit-animation: other 1s ease-in-out;
          animation: other 1s ease-in-out;
}

.text-transform-2 {
  -webkit-animation: other 1s ease-in-out;
          animation: other 1s ease-in-out;
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
}

.text-transform-3 {
  -webkit-animation: upside 1s ease-in-out;
          animation: upside 1s ease-in-out;
  -webkit-animation-delay: 0.7s;
          animation-delay: 0.7s;
}

.hide {
  visibility: hidden;
}

.animation-delay-1s {
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}

@-webkit-keyframes other {
  0% {
    opacity: 0;
    -webkit-transform-origin: center;
            transform-origin: center;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@keyframes other {
  0% {
    opacity: 0;
    -webkit-transform-origin: center;
            transform-origin: center;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@-webkit-keyframes upside {
  0% {
    opacity: 0;
    -webkit-transform-origin: center;
            transform-origin: center;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@keyframes upside {
  0% {
    opacity: 0;
    -webkit-transform-origin: center;
            transform-origin: center;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@-webkit-keyframes moveUp {
  from {
    background-color: blue;
    height: 0px;
  }
  to {
    background-color: blue;
    height: 100%;
  }
}

@keyframes moveUp {
  from {
    background-color: blue;
    height: 0px;
  }
  to {
    background-color: blue;
    height: 100%;
  }
}

@media screen and (max-width: 800px) {
  #about .container .title {
    width: 90%;
    font-size: 3em;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  #about .container .about-me {
    width: 90%;
    font-size: 1.5em;
  }
}

@media screen and (max-width: 300px) {
  .landing__container .landing__title {
    width: 90%;
    font-size: 2em;
  }
  .landing__subtitle {
    font-size: 0.8em;
  }
}

