#work {
  width: auto;
  height: 100vh;
  overflow: hidden;
}

#work .work-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center !important;
  align-items: center !important;
  overflow-x: hidden;
  background-color: #121212;
}

.work-container .work-title {
  font-size: 5rem;
  font-weight: bold;
  color: white;
  margin-bottom: 40px;
}

.work-container .work-details {
  width: 75%;
  height: auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.work-container .work-details .left-titles {
  width: 30%;
  height: 100%;
}

.work-container .work-details .left-titles {
  font-size: 1.2em;
  color: gray;
}

.work-container .work-details .left-titles ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.work-container .work-details .left-titles ul li {
  cursor: pointer;
  width: 70%;
  margin: 10px;
  line-break: strict;
}

.work-container .work-details .left-titles ul .active {
  color: white;
  border-left: 2px solid whitesmoke;
  padding-left: 10px;
  text-overflow: ellipsis;
}

.work-container .work-details .right-desc {
  width: 70%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.work-container .work-details .right-desc .details {
  color: white;
}
#work .work-container .resume {
  margin-top: 40px;
  padding: 10px;
  width: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow-x: hidden;
  background-color: white;
  cursor: pointer;
  border-radius: 6px;
  transition: 100ms ease-in-out;
}

#work .work-container .resume:hover {
  background-color: lightgray;
}

#work .work-container .resume a {
  color: black;
  text-decoration: none;
}

